import { Injectable, Inject } from '@angular/core';
import { Http, Headers, RequestOptions } from '@angular/http';
import 'rxjs/add/operator/map';
import { ServiceUrl } from './ServiceUrl';
@Injectable()
export class AddCategoryService {
    urlService = new ServiceUrl();
    constructor(private http: Http) {

    }
    addCategory(chemistId,categoryName,ParentId) {
        let body = JSON.stringify({ChemistId:chemistId,CategoryName:categoryName,ParentId:ParentId});
        console.log(body);
        let headers = new Headers({ 'Content-Type': 'application/json' });
        let options = new RequestOptions({ method: 'post', headers: headers });

        return this.http.post(this.urlService.baseUrl + "Chemist/addCategory", body, options)
            .map(res => res.json());
    }
    getAllSubCategoryValues(ChemistId,CategoryId)
    {
        let body = JSON.stringify({ChemistId:ChemistId,ParentId:CategoryId});
        let headers = new Headers({ 'Content-Type': 'application/json' });
        let options = new RequestOptions({ method: 'get', headers: headers });

        return this.http.get(this.urlService.baseUrl + "Chemist/getAllCategoriesAndSubCategories", options)
            .map(res => res.json());
    }
    getCateogry(chemistId)
    {
        let body = JSON.stringify({});
        let headers = new Headers({ 'Content-Type': 'application/json' });
        let options = new RequestOptions({ method: 'get', headers: headers });

        return this.http.get(this.urlService.baseUrl + "Chemist/getCategoriesByChemistId?chemistId="+chemistId, options)
            .map(res => res.json());

    }
    
    addSubcategory(chemistId,subCategoryName,parentId)
    {

        let body = JSON.stringify({ ChemistId: chemistId, SubCategoryName: subCategoryName,ParentId:parentId });
        let headers = new Headers({ 'Content-Type': 'application/json' });
        let options = new RequestOptions({ method: 'post', headers: headers });

        return this.http.post(this.urlService.baseUrl + "Chemist/addSubCategory", body, options)
            .map(res => res.json());


    }

  
   
}


