import { Component, ViewEncapsulation } from '@angular/core';
import { Router } from '@angular/router';
import { AddCategoryService } from './../services/AddCategoryService';
import {categoryModel} from './../models/categoryModel';
@Component({
  selector: 'chemist',
  providers: [AddCategoryService],
  styles: [ require('./chemist.style.scss') ],
  template: require('./chemist.template.html'),
  encapsulation: ViewEncapsulation.None,
  host: {
    class: 'chemist-page app'
  },
})
export class ChemistComponent {
  router: Router;
  public chemistId:any;
  public parentId:any;
  public subCategoryName:any;
public categoryName:any;
public catergories : categoryModel[]=[];
public singleCategory:categoryModel;
public categoryId:any;
public UserType:any;

  constructor(public _addCategoryService:AddCategoryService, router: Router,) {
    this.UserType=localStorage.getItem("UserType");
    if(this.UserType==null)
    {
      this.router.navigate(["/app/login"]);
    
    }
    else
    {
    this.router = router;
    this.chemistId=localStorage.getItem("GetChemistId");
    this._addCategoryService.getCateogry(this.chemistId).subscribe(
      response => {
    
      this.catergories=response.data;
   
   
    });
  }
    }

  addCategoryValues()
  {
    this.chemistId=localStorage.getItem("GetChemistId");
    this.parentId=0;
    
    this._addCategoryService.addCategory(this.chemistId,this.categoryName,this.parentId).subscribe(
      response => {
       
        jQuery("#snackbar1").html(response.message);
        this.myFunction();
        if(response.message=="SUCCESS")
        {
          this._addCategoryService.getCateogry(this.chemistId).subscribe(
      response => {
      //  console.log(response);
      this.catergories = response.data;
      });
      this.categoryName=undefined;
    }
    else{

      jQuery("#snackbar1").html("Not A Valid Category");
      this.myFunction();
    }
        
  });
    

  }
  addSubcateogry()
  {
    this.chemistId=localStorage.getItem("GetChemistId");
    this.parentId=1;
      if(this.chemistId!=undefined && this.parentId!=undefined  && this.subCategoryName!=undefined) {
        this._addCategoryService.addSubcategory(this.chemistId,this.subCategoryName,this.categoryId).subscribe(
          response => {
         
            jQuery("#snackbar1").html(response.message);
            this.myFunction();
            
          });
          this.subCategoryName=undefined;
      }
      else
      {

        jQuery("#snackbar1").html("Invalid Credenntials");
        this.myFunction();
      }

   
    
  }
  searchResult(): void {
    this.router.navigate(['/app', 'extra', 'search']);
  }
  getCategoryId(chemistid)
  {
this.chemistId=chemistid;

  }

  getCategory(categoryId)
  {
   
    this.categoryId = categoryId;


  }
  myFunction() {
    // Get the snackbar DIV
    var x = document.getElementById("snackbar1")

    // Add the "show" class to DIV
    x.className = "show";

    // After 3 seconds, remove the show class from DIV
    setTimeout(function(){ x.className = x.className.replace("show", ""); }, 3000);
}
}



